* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background-color: rgb(241, 241, 219);
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  padding: 30px;
}

h1 {
  margin: 10px 0;
}

p {
  font-size: 16px;
  line-height: 25px;
}

.container {
  background-color: rgb(199, 204, 100);
  border-radius: 10px;
  box-shadow: 0px 10px 20px -10px (0, 0, 0, 0.75);
  color: #1d338a;
  padding-top: 30px;
  position: relative;
  width: 350px;
  max-width: 100%;
  text-align: center;
  height: 550px;
}

.card img {
 /* border: 1px solid #03bf03; */
 border-radius:90%;
 padding: 9px;
 width:200px ;
}

.btns button {
  background-color:rgb(149, 177, 110);
  /* color: brown; */
  border-radius: 4px;
  font-size: 17px;
  padding: 0.4em;
  margin-right: 10px;
}

.styling {
  font-family:sans-serif ;
  padding: 10px;
}

.tech {
  color: green;
  font-weight: bold;
}

.like {
  background: none;
  border: none;
}

.fa-heart {
  color: red;
  font-size: 14px;
}